*{
  box-sizing: border-box;
}
.navbar {
  width: 100vw;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
}

.navbar ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 4% 0 2%;
}

.navbar ul li {
  list-style: none;
}

.navbar ul li img {
  width: 20%;
  height: 100%;
}

.navbar ul li a {
  text-decoration: none;
}

.navbar ul li button {
  padding: 10px 20px;
  width: 100%;
  background-color: yellow;
  color: black;
  font-weight: 800;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .navbar {
      flex-direction: column;
      align-items: flex-start;
      height: auto;
  }

  .navbar ul {
    padding: 1% 3% 0% 1%;
  }

  .navbar ul li {
      margin-bottom: 10px;
  }

  .navbar ul li img {
    width: 30%;
    padding: 0px 0 0 2%;
  }
}

@media (max-width: 480px) {
  .navbar ul {
      flex-direction: row;
      padding: 3% 4% 1% 4%;
  }

  /* .navbar ul li {
      margin-bottom: 10px;
  } */

  .navbar ul li img {
    width: 50%;
  }

  
}

